<template>
  <div class="planning">
    <div id="planningContainer" class="row main">
      <div class="col-xl-4" v-if="canEditName">
        <SearchUser v-on:loadUser="loadUser"/>
      </div>
      <div v-bind:class="{'col-xl-8': canEditName, 'container': !canEditName}">
        
        <h1>Planning</h1>
        <div  id="PlanningComponentFromPlanning">
          <PlanningComponent v-bind:user="user" v-bind:mondayDate="mondayDate"/>
        </div>
        


      </div> 
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import SearchUser from '@/components/SearchUser.vue'
import PlanningComponent from '@/components/PlanningComponent.vue'

export default {
  name: 'planning',
  components: {
    SearchUser,
    PlanningComponent
  },
  created : function() {
    this.user = this.$root.getLoggedUser()
    this.canEditName = this.$root.isDRH(this.user) ||  this.$root.isAdmin(this.user)
    this.mondayDate = this.$root.formatDate(new Date(new Date().getTime() - (new Date().getDay()-1) * 24 * 60 * 60 * 1000 ))
  },
  data : function(){
    return {
      canEditName : false,
      user : {},
      mondayDate : "2018-11-19"
    }
  },
  methods : {
    loadUser : function(user){
      //alert("loadUser")
      this.user = user
      // console.log(user)
    }

  },
  computed : {},
  watch : {}
};
</script>



<style scoped>

.planning {
  margin-top: 1em;
}

#planningContainer {
  margin: 0px;
}

@media (max-width: 576px) {

  #PlanningComponentFromPlanning {
    overflow-x: scroll;
    overflow-y: hidden;
    padding-bottom: 5px;
  }

}

</style>